body {
  font-family: "Noto Sans JP";
  color: #444444;
  line-height: 1.8;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.concept-attempt {
  margin-bottom: 3.75rem;
}
.concept-attempt__inner {
  max-width: 1100px;
  padding: 0 1rem;
}
.concept-attempt__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(50% - 50vw);
  width: calc(100vw - 1rem);
  margin-bottom: 2.5rem;
}
.concept-attempt__title {
  order: 1;
  color: #e5e5e5;
  font-size: 2rem;
  font-weight: 400;
  writing-mode: vertical-rl;
  width: 3.25rem;
  line-height: 3.25rem;
  position: relative;
}
.concept-attempt__title::after {
  content: "";
  width: 0.0625rem;
  height: 10rem;
  position: absolute;
  top: 8.4375rem;
  left: 45%;
  background-color: #e5e5e5;
}
.concept-attempt__image {
  width: calc(100% - 3.25rem);
  text-align: right;
}
.concept-attempt__text {
  font-size: 0.8125rem;
}